.card--bg {
  background-color: #e5e5e5;
  padding: 1em;
}

.card__sub {
  margin-bottom: 1em;
}

.info-box {
  background-color: #e5e5e5;
  margin-bottom: 2%;
  padding: 1em;
  width: 100%;
}

@media (min-width: 48em) {
  .info-box {
    flex: 1 0 43%;
    margin: 0 2% 2% 0;
    width: auto;
  }
  .info-box:nth-child(2n+2), .info-box:last-child {
    margin-right: 0;
  }
}

.info-box__title {
  color: #666;
  font-weight: 400;
  text-align: center;
}

.breadcrumb {
  list-style: none;
  margin: 0;
  padding: 0;
}

.breadcrumb__item,
.breadcrumb__link,
.breadcrumb__link:link,
.breadcrumb__link:visited,
.breadcrumb__link:focus {
  color: #999;
  text-decoration: none;
}

.breadcrumb__link:hover {
  color: #666;
}

.breadcrumb__item {
  display: inline-block;
  font-size: .75rem;
}

.breadcrumb__item::after {
  content: "/";
}

.breadcrumb__item:last-child {
  color: #bbb;
}

.breadcrumb__item:last-child::after {
  content: "";
}

.breadcrumb_link:active {
  color: #bbb;
}

.menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu__item {
  margin: 0 1em .5em 0;
}

@media (min-width: 48em) {
  .menu__item {
    display: inline;
    margin-bottom: 1em;
  }
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  font-size: 7.5px;
  font-size: 0.75rem;
  font-weight: 600;
  letter-spacing: 1.5px;
  text-decoration: none;
  text-transform: uppercase;
}

.tabs__nav {
  list-style: none;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #e5e5e5;
}

@media (min-width: 48em) {
  .tabs__nav {
    border-left: 1px solid #e5e5e5;
    display: flex;
  }
}

.tabs__tab {
  display: none;
}

.tabs__tab.is-active {
  display: block;
}

.tabs__content {
  padding: 1.5rem;
}

.tabs.no-js .tabs__tab.is-active {
  display: block;
}

.tabs__nav--local-tasks {
  margin: 1em 0 .5em;
  padding: 0;
}

.tabs__link--local-tasks {
  padding: .3em 1.5em;
}

.pager {
  margin: 2em 0;
}

.pager__items {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

.pager__item {
  display: inline-block;
  margin: 0 .7em;
}

@media (min-width: 48em) {
  .pager__item {
    margin: 0 1em;
  }
}

.pager__link,
.pager__link:link,
.pager__link:visited {
  color: #999;
  font-weight: 600;
  text-decoration: none;
}

.pager__link.is-active, .pager__link:hover,
.pager__link:link.is-active,
.pager__link:link:hover,
.pager__link:visited.is-active,
.pager__link:visited:hover {
  color: #666;
}

.pager__link--next,
.pager__link--prev {
  display: block;
}

.pager__link--next span,
.pager__link--prev span {
  display: none;
}

.pager__link--next::before,
.pager__link--prev::before {
  border: 8px solid #999;
  border-bottom: 6px solid transparent;
  border-right: 7px solid transparent;
  border-top: 6px solid transparent;
  content: "";
  display: block;
  position: relative;
  top: 1px;
}

.pager__link--next:hover::before,
.pager__link--prev:hover::before {
  border-left-color: #666;
}

.pager__link--prev::before {
  border-left-color: transparent;
  border-right-color: #999;
}

.pager__link--prev:hover::before {
  border-left-color: transparent;
  border-right-color: #666;
}

.status--warning {
  background-color: #f9fb93;
  color: shade(#f9fb93, 60%);
  display: block;
  margin-bottom: 0.75em;
  padding: 0.75em;
  text-align: center;
}

.status--warning a {
  color: shade(#f9fb93, 70%);
  text-decoration: underline;
}

.status--warning a:focus, .status--warning a:hover {
  color: shade(#f9fb93, 90%);
}

.status--warning .placeholder {
  font-style: normal;
  font-weight: 800;
}

.status--error {
  background-color: #fbe3e4;
  color: shade(#fbe3e4, 60%);
  display: block;
  margin-bottom: 0.75em;
  padding: 0.75em;
  text-align: center;
}

.status--error a {
  color: shade(#fbe3e4, 70%);
  text-decoration: underline;
}

.status--error a:focus, .status--error a:hover {
  color: shade(#fbe3e4, 90%);
}

.status--error .placeholder {
  font-style: normal;
  font-weight: 800;
}

.status--status {
  background-color: #cfefc2;
  color: shade(#cfefc2, 60%);
  display: block;
  margin-bottom: 0.75em;
  padding: 0.75em;
  text-align: center;
}

.status--status a {
  color: shade(#cfefc2, 70%);
  text-decoration: underline;
}

.status--status a:focus, .status--status a:hover {
  color: shade(#cfefc2, 90%);
}

.status--status .placeholder {
  font-style: normal;
  font-weight: 800;
}

.pl .patternLink {
  color: black;
}

.pl .visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px;
  width: 1px;
  word-wrap: normal;
}

.pl .sg-pattern {
  clear: left;
}

.pl #icons {
  display: flex;
  flex-wrap: wrap;
}

.pl #icons > div {
  border: 1px solid #eee;
  padding: 1em;
  text-align: center;
}
